import React from 'react';

const MindMapConnection = ({ word, parent, hoveredWord, clickedWord }) => {
  if (!word.parentId || !parent) {
    return null;
  }

  const midX = (parent.x + word.x) / 2;
  const midY = (parent.y + word.y) / 2;
  const controlOffset = 30;
  
  // Highlight state determination - only highlight directly connected lines
  const isHoveredHighlighted = hoveredWord && 
    (hoveredWord.id === word.id || hoveredWord.id === parent.id);
  
  const isClickedHighlighted = clickedWord && 
    (clickedWord.id === word.id || clickedWord.id === parent.id);
  
  const isHighlighted = isHoveredHighlighted || isClickedHighlighted;
  const isDimmed = (hoveredWord || clickedWord) && !isHighlighted;
  
  return (
    <path
      d={`M ${parent.x} ${parent.y} Q ${midX} ${midY - controlOffset} ${word.x} ${word.y}`}
      stroke={isHighlighted ? "#3b82f6" : "#64748b"}
      strokeWidth={isHighlighted ? "3" : "2"}
      fill="none"
      opacity={isDimmed ? "0.2" : isHighlighted ? "1" : "0.6"}
      className="connection-line"
    />
  );
};

export default MindMapConnection;
