import React from 'react';

const MindMapNode = ({ 
  word, 
  selectedWord, 
  hoveredWord, 
  clickedWord,
  connectedNodes, 
  clickedConnectedNodes,
  onMouseEnter, 
  onMouseLeave, 
  onClick 
}) => {
  const getNodeWidth = (text) => Math.max(text.length * 8 + 20, 80);
  const getNodeHeight = () => 35;

  const width = getNodeWidth(word.word);
  const height = getNodeHeight();
  const isSelected = selectedWord?.id === word.id;
  const isHovered = hoveredWord?.id === word.id;
  const isClicked = clickedWord?.id === word.id;
  
  // Highlight state determination
  const isHoveredConnected = hoveredWord && (hoveredWord.id === word.id || connectedNodes.has(word.id));
  const isClickedConnected = clickedWord && (clickedWord.id === word.id || clickedConnectedNodes.has(word.id));
  const isConnected = isHoveredConnected || isClickedConnected;
  const isDimmed = (hoveredWord || clickedWord) && !isConnected;
  
  return (
    <g 
      className="mindmap-node"
      onMouseEnter={() => onMouseEnter(word)}
      onMouseLeave={() => onMouseLeave(null)}
    >
      <rect
        x={word.x - width/2}
        y={word.y - height/2}
        width={width}
        height={height}
        rx={6}
        fill={word.level === 0 ? '#6366f1' : word.level === 1 ? '#8b5cf6' : '#ec4899'}        stroke={isSelected ? '#f59e0b' : (isHovered || isClicked) ? '#60a5fa' : isConnected ? '#3b82f6' : '#cbd5e1'}
        strokeWidth={isSelected ? 3 : (isHovered || isClicked || isConnected) ? 2 : 1}
        opacity={isDimmed ? 0.3 : 1}
        style={{ 
          cursor: 'pointer',          filter: isSelected ? 'drop-shadow(0 4px 12px rgba(245, 158, 11, 0.4))' : 
                  (isHovered || isClicked || isConnected) ? 'drop-shadow(0 2px 8px rgba(96, 165, 250, 0.3))' : 'none'
        }}
        onClick={(e) => onClick(e, word)}
      />
      <text
        x={word.x}
        y={word.y - (word.level > 0 ? 5 : 0)}
        textAnchor="middle"
        dominantBaseline="middle"
        fill="white"
        fontSize="13"
        fontWeight="500"
        opacity={isDimmed ? 0.3 : 1}
        style={{ 
          pointerEvents: 'none',
          userSelect: 'none'
        }}
      >
        {word.word}
      </text>          {/* Display meaning for level 1 and above */}
      {word.level > 0 && (
        <text
          x={word.x}
          y={word.y + 8}
          textAnchor="middle"
          dominantBaseline="middle"
          fill="rgba(255,255,255,0.8)"
          fontSize="9"
          opacity={isDimmed ? 0.3 : 1}
          style={{ 
            pointerEvents: 'none',
            userSelect: 'none'
          }}
        >
          {word.meaning}
        </text>
      )}
    </g>
  );
};

export default MindMapNode;
