import React, { useEffect } from "react";
import Skill from "./skill";
import About from "./about";
import Statement from "./statement";
import Contact from "./contact";

export default function Main() {
  // Smooth scroll function
  const scrollToSection = (targetId) => {
    const targetElement = document.getElementById(targetId);
    if (targetElement) {
      targetElement.scrollIntoView({
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  // Scroll to top function
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };
  // Handle navigation clicks
  const handleNavClick = (e, targetId) => {
    e.preventDefault(); // Prevent default anchor behavior
    scrollToSection(targetId);
  };

  // Handle page-top button click
  const handlePageTopClick = (e) => {
    e.preventDefault();
    scrollToTop();
  };

  useEffect(() => {
    // Add event listener for page-top button
    const pageTopButton = document.getElementById('js-page-top');
    if (pageTopButton) {
      pageTopButton.addEventListener('click', handlePageTopClick);
      
      // Cleanup
      return () => {
        pageTopButton.removeEventListener('click', handlePageTopClick);
      };
    }
  }, []);

  return (
    <div>
      <nav className="gnav">
        <div className="gnav-inner">
          <ul className="gnav-list">
            <li className="gnav-item">
              <a href="#about" onClick={(e) => handleNavClick(e, 'about')}>About</a>
            </li>
            <li className="gnav-item">
              <a href="#statement" onClick={(e) => handleNavClick(e, 'statement')}>Statements</a>
            </li>
            <li className="gnav-item">
              <a href="#skill" onClick={(e) => handleNavClick(e, 'skill')}>Skills</a>
            </li>
            <li className="gnav-item">
              <a href="#contact" onClick={(e) => handleNavClick(e, 'contact')}>Contact</a>
            </li>
          </ul>
        </div>
      </nav>
      <section className="about section" id="about">
        <About />
      </section>
      <section className="statement section" id="statement">
        <Statement />
      </section>
      <section className="skill section" id="skill">
        <Skill />
      </section>
      <section className="contact section" id="contact">
        <Contact />
      </section>
      <div className="page-top" id="js-page-top" onClick={handlePageTopClick} style={{ cursor: 'pointer' }}>
        <span className="material-icons-outlined">expand_less</span>
      </div>
    </div>
  );
}
