import React, { useState, useRef, useEffect } from 'react';
import {
  ThemeProvider,
  createTheme,
  CssBaseline,
  Container,
  AppBar,
  Toolbar,
  Typography,
  Box,
  Paper,
  IconButton
} from '@mui/material';
import ZoomInIcon from '@mui/icons-material/ZoomIn';
import ZoomOutIcon from '@mui/icons-material/ZoomOut';
import CenterFocusStrongIcon from '@mui/icons-material/CenterFocusStrong';
import MindMapNode from './components/MindMapNode';
import MindMapConnection from './components/MindMapConnection';
import './styles/MindMapApp.css';

const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#6366f1',
    },
    secondary: {
      main: '#ec4899',
    },
    background: {
      default: '#f8fafc',
    },
  },
  typography: {
    h4: {
      fontWeight: 600,
    },
  },
  components: {
    MuiPaper: {
      styleOverrides: {
        root: {
          borderRadius: 8,
        },
      },
    },
  },
});

// sample data
const sampleWords = [
  { id: '1', word: 'Animal', meaning: '動物', x: 400, y: 200, level: 0, parentId: null },
  { id: '2', word: 'Mammals', meaning: '哺乳類', x: 250, y: 150, level: 1, parentId: '1' },
  { id: '3', word: 'Birds', meaning: '鳥類', x: 250, y: 250, level: 1, parentId: '1' },
  { id: '4', word: 'Fish', meaning: '魚類', x: 550, y: 150, level: 1, parentId: '1' },
  { id: '5', word: 'Reptiles', meaning: '爬虫類', x: 550, y: 250, level: 1, parentId: '1' },
  { id: '6', word: 'Dog', meaning: '犬', x: 100, y: 100, level: 2, parentId: '2' },
  { id: '7', word: 'Cat', meaning: '猫', x: 100, y: 200, level: 2, parentId: '2' },
  { id: '8', word: 'Eagle', meaning: '鷲', x: 100, y: 250, level: 2, parentId: '3' },
  { id: '9', word: 'Shark', meaning: 'サメ', x: 700, y: 150, level: 2, parentId: '4' }
];

export default function MindMapApp() {
  const [words, setWords] = useState(sampleWords);
  const [selectedWord, setSelectedWord] = useState(null);
  const [hoveredWord, setHoveredWord] = useState(null);
  const [clickedWord, setClickedWord] = useState(null);
  const [zoom, setZoom] = useState(1);
  const canvasRef = useRef(null);
  // Zoom operations
  const handleZoomIn = () => setZoom(prev => Math.min(prev + 0.2, 3));
  const handleZoomOut = () => setZoom(prev => Math.max(prev - 0.2, 0.3));
  const handleResetView = () => {
    setZoom(1);
  };

  // Get connected nodes on hover
  const getConnectedNodes = (nodeId) => {
    const connectedNodes = new Set();
    
    // Get child nodes
    words.forEach(word => {
      if (word.parentId === nodeId) {
        connectedNodes.add(word.id);
      }
    });
    
    // Get parent node
    const node = words.find(w => w.id === nodeId);
    if (node && node.parentId) {
      connectedNodes.add(node.parentId);
    }
    
    return connectedNodes;
  };

  // Mouse wheel zoom handling
  useEffect(() => {
    const handleWheel = (e) => {
      if (e.ctrlKey) {
        e.preventDefault();
        const delta = e.deltaY < 0 ? 0.1 : -0.1;
        setZoom(prev => Math.max(0.3, Math.min(3, prev + delta)));
      }
    };

    const canvas = canvasRef.current;
    if (canvas) {
      canvas.addEventListener('wheel', handleWheel, { passive: false });
      return () => canvas.removeEventListener('wheel', handleWheel);
    }
  }, []);  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.key === 'Escape') {
        setSelectedWord(null);
        setClickedWord(null);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);  }, []);

  // Background click handler
  const handleBackgroundClick = (e) => {
    setSelectedWord(null);
    setClickedWord(null);
  };

  // Node mouse events
  const handleNodeClick = (e, word) => {
    e.stopPropagation();
    setSelectedWord(word);
    setClickedWord(word);
  };
  // Render connection lines
  const renderConnections = () => {
    return words.map(word => {
      const parent = words.find(w => w.id === word.parentId);
      return (
        <MindMapConnection
          key={`line-${word.id}`}
          word={word}
          parent={parent}
          hoveredWord={hoveredWord}
          clickedWord={clickedWord}
        />
      );
    });
  };
  // Render word nodes
  const renderWordNodes = () => {
    return words.map(word => {
      const connectedNodes = hoveredWord ? getConnectedNodes(hoveredWord.id) : new Set();
      const clickedConnectedNodes = clickedWord ? getConnectedNodes(clickedWord.id) : new Set();
      
      return (
        <MindMapNode
          key={word.id}
          word={word}
          selectedWord={selectedWord}
          hoveredWord={hoveredWord}
          clickedWord={clickedWord}
          connectedNodes={connectedNodes}
          clickedConnectedNodes={clickedConnectedNodes}
          onMouseEnter={setHoveredWord}
          onMouseLeave={setHoveredWord}
          onClick={handleNodeClick}
        />
      );    });
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box sx={{ flexGrow: 1, minHeight: '100vh', bgcolor: 'background.default' }}>
        <AppBar position="static" elevation={1} sx={{ bgcolor: '#6366f1' }}>
          <Toolbar>
            <Typography variant="h4" component="div" sx={{ flexGrow: 1 }}>
              Mind Map
            </Typography>            <Box sx={{ display: 'flex', gap: 1 }}>
              <IconButton color="inherit" onClick={handleZoomOut} title="Zoom Out">
                <ZoomOutIcon />
              </IconButton>
              <IconButton color="inherit" onClick={handleZoomIn} title="Zoom In">
                <ZoomInIcon />
              </IconButton>
              <IconButton color="inherit" onClick={handleResetView} title="Reset View">
                <CenterFocusStrongIcon />
              </IconButton>
            </Box>
          </Toolbar>
        </AppBar>
        
        <Container maxWidth="xl" sx={{ mt: 2, mb: 2 }}>
          <Paper 
            elevation={2}
            sx={{ 
              height: '80vh',              
              position: 'relative', 
              overflow: 'hidden',
              bgcolor: '#fefefe'
            }}
          >            
          <svg
              ref={canvasRef}
              width="100%"
              height="100%"
              onClick={handleBackgroundClick}
              style={{
                transform: `scale(${zoom})`,
                transformOrigin: '0 0'
              }}
            >
              <defs>
                <pattern id="grid" width="20" height="20" patternUnits="userSpaceOnUse">
                  <path d="M 20 0 L 0 0 0 20" fill="none" stroke="#e5e7eb" strokeWidth="0.5" opacity="0.3"/>
                </pattern>
              </defs>
              <rect width="100%" height="100%" fill="url(#grid)" />
              {renderConnections()}
              {renderWordNodes()}
            </svg>
          </Paper>
          
          {selectedWord && (
            <Paper 
              elevation={3} 
              sx={{ 
                position: 'absolute', 
                bottom: 20, 
                left: 20, 
                p: 2, 
                minWidth: 250,
                bgcolor: 'background.paper',
                borderLeft: '4px solid #6366f1'
              }}
            >
              <Typography variant="h6" sx={{ color: '#6366f1' }}>{selectedWord.word}</Typography>
              <Typography variant="body2" color="text.secondary">
                {selectedWord.meaning}
              </Typography>
              <Typography variant="caption" display="block" sx={{ mt: 1, color: '#64748b' }}>
                Level: {selectedWord.level} | Children: {words.filter(w => w.parentId === selectedWord.id).length}
              </Typography>              <Box sx={{ mt: 1 }}>
                <Typography variant="caption" sx={{ color: '#64748b' }}>
                  Ctrl+Wheel: Zoom | Escape: Deselect | Click: Highlight
                </Typography>
              </Box>
            </Paper>
          )}        </Container>
      </Box>
    </ThemeProvider>
  );
}
