import React from "react";
import SkillItem from "./skillItem";

export default function Skill() {
  return (
    <div class="inner">
      <h2 class="title">Skills</h2>
      <div class="skill-list">
        <SkillItem
          urls={["img/logo_react.png", "img/logo_vue.png"]}
          name="Frontend"
        >
          I have basic knowledge of HTML, CSS, and JavaScript, and I can create
          web pages. I am also capable of building single-page applications
          (SPAs) using JavaScript libraries such as Vue and React.
        </SkillItem>
        <SkillItem
          urls={["img/logo_Java.png", "img/logo_spring.png"]}
          name="Backend"
        >
          In my previous work, I mainly developed APIs and server-side rendered
          (SSR) applications using the Spring Framework in Java. I have also
          personally created APIs using the Express framework in JavaScript.{" "}
        </SkillItem>
        <SkillItem urls={["img/logo_aws.png"]} name="Infrastructure">
          I have hands-on experience in building, managing, and maintaining
          infrastructure using AWS.
        </SkillItem>
        <SkillItem
          urls={[
            "img/logo_oracle.png",
            "img/logo_postgresql.png",
            "img/logo_mysql.png",
          ]}
          name="Database"
        >
          I have worked with databases such as Oracle, PostgreSQL, and MySQL,
          and have a solid understanding of database concepts and SQL.
        </SkillItem>
        <SkillItem urls={["img/logo_mathematica.png"]} name="Mathematics">
          I hold a Master’s degree in Science (Mathematics) and approach
          problem-solving with logical thinking.
        </SkillItem>
        <SkillItem
          urls={["img/logo_github.png", "img/logo_docker.png"]}
          name="Others"
        >
          Git, GitHub, Docker
        </SkillItem>
      </div>
    </div>
  );
}
