import React from "react";

export default function About() {
  return (
    <div className="inner">
      {" "}
      <h2 className="title">About</h2>
      <div className="profile">
        <p className="profile-img">
          <img src="img/profile.jpeg" alt="Profile" />
        </p>
        <div className="profile-body">
          <p>
            I have worked as a systems engineer (SE) for three years, during
            which I gained comprehensive knowledge of web development, including
            front-end, back-end, and infrastructure. Driven by curiosity, I
            actively explored new web technologies beyond the scope of my
            assigned tasks. In university and graduate school (Master’s
            program), I majored in mathematics and conducted research on knot
            theory.
          </p>
        </div>
      </div>
    </div>
  );
}
