import React from "react";

export default function Contact() {
  return (
    <div class="inner">
      <h2 class="title">Contact</h2>
      <p class="lead">If you have any inquiries, feel free to contact me through LinkedIn.</p>
      <div class="contact-list">
        <a
          class="contact-item"
          href="https://www.linkedin.com/in/tsuyoshi-aita-5345a3357/"
          rel="noopener"
        >
          Link
        </a>
      </div>
    </div>
  );
}
